.header {
    padding: $l-size 0;
    background: fade-out(white, 1);
}

.header__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__content__logo {
    font-family: 'El Messiri', sans-serif;
    font-size: 3.0rem;
    color: white;
    justify-content: center;
}

.header--link {
    color: white;
    text-decoration: none;
    font-family: 'Montserrat', sans-serif;
    position: relative;
    &:hover {
        color: white;
    }
    &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        bottom: 0;
        left: 0;
        background-color: white;
        visibility: hidden;
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
        -webkit-transition: all 0.3s ease-in-out 0s;
        transition: all 0.3s ease-in-out 0s;
    }
    &:hover:before {
        visibility: visible;
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
}

.header--link--spacing {
    margin-right: $xl-size;
}

.header--icon--spacing {
    margin-left: $l-size;
}

.header--icon {
    color: white;
    &:hover {
        color: #6b9ae5;
    }
}



