.about-container .header {
    background: black;
    margin-bottom: 10rem;
}

.headshot {
    height: 41.5rem;
    width: 34rem;
    border: 10px solid black;
    padding: 10px;
}

.about {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 100vh;
    width: 100vw;
    @media (max-width: $desktop-breakpoint) {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
    }
}

.about__bio {
    width: 45vw;
    font-family: 'Montserrat', sans-serif;
    h1 {
        margin-top: 0;
        margin-bottom: 0;
    }
    @media (max-width: $desktop-breakpoint) {
        width: 90vw;
        margin-top: $xxl-size;
    }
}


